import { useEffect } from "react";

const FlexibleHours = () => {
	useEffect(() => {
		document.title = "Flexible Hours";
	}, []);
	return (
		<div className="outer-container">
			<div className="job-detail-container">
				<h1 className="job-details-title">Flexible Hours</h1>
				<p>
					Hey, some people are morning people and some people arent, so we
					dont really care when you start your day but you need to be able to
					coordinate your schedule with your team. If most of your team gets to
					the office at 7 am and youre showing up at 11 am, that probably wont
					be sustainable. Its common sense. Youll need to show up when youre
					needed or convince your team to sleep in a little later.
				</p>
				<p>
					Were all Computer Employees exempt from overtime pay, but our goal
					is to never have to ask you to work over 40 hours. We know continually
					working a ton of hours is not sustainable. Wed like to think you can
					get more done when youre fresh and happy.
				</p>
				<p>
					We have a hybrid approach to our work. Some of us live near offices, a
					lot of us dont. That said, we generally have new hires come to
					Spokane for about a week to meet the local team. We like to think that
					were smarter when we work together. Collaboration and teamwork are
					key to how we work at Two Barrels.
				</p>
			</div>
		</div>
	);
};

export default FlexibleHours;
